<template>
    <v-modal
        confirm-modal
        :show-modal="showModal"
        @close-modal="closeModal"
        @openedModal="eventClick"
    >
        <template #header>
            <h2 class="modal__header-title">
                <lock /> Authorize
            </h2>
        </template>
        <template #content>
            <div class="action-block">
                <p>Enter your account password to authorize:</p>
                <form @submit.prevent="validate">
                    <div class="modal__input-mask">
                        <div class="input-field">
                            <form-input
                                id="modalPass"
                                ref="pass"
                                v-model="$v.password.$model"
                                :field.sync="password"
                                name="password"
                                autofocus
                                legend="Password"
                                prepend-icon="LockIcon"
                                :append-icon="showPassword ? 'EyeIcon' : 'EyeOffIcon'"
                                :type="showPassword ? 'text' : 'password'"
                                :errors="validationMessage($v.password)"
                                :is-valid="$v.password.$dirty && !$v.password.$anyError"
                                @clicked="showPassword = !showPassword"
                                @input.native="$v.password.$touch()"
                                @blur.native="$v.password.$touch()"
                            />
                        </div>
                    </div>
                </form>
            </div>
        </template>
        <template #footer>
            <custom-button
                default
                :disabled="!password"
                class="button btn-base_colored"
                :loading="loading"
                :type="'submit'"
                @on-btn-click="validate"
            >
                Confirm
            </custom-button>
        </template>
    </v-modal>
</template>

<script>
import VModal from '@/components/modals/VModal';
import Lock from 'mdi-vue/Lock'
import { eventBus } from '@/helpers/event-bus/'
import Api from '@/helpers/api/index.js'
import { validationMixin } from 'vuelidate';
import formValidationMixin from '@/mixins/formValidationMixin';
import { validationMessage } from 'vuelidate-messages';
import { formMessages } from '@/validation/login/Messages';

export default {
    name: 'AuthModal',
    components: {
        VModal,
        Lock
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    props: {
        showModal: Boolean
    },
    validations: {
        password: {
            required: true
        }
    },
    data: () => ({
        showPassword: false,
        loading: false,
        password: null,
        error: false,
        message: null
    }),
    methods: {
        validationMessage: validationMessage(formMessages),
        submit() {
            this.loading = true
            Api.post('/api/client/verify-password', {
                password: this.password
            })
                .then(() => {
                    this.closeModal()
                    this.$emit('confirm')
                })
                .catch((error) => {
                    eventBus.$emit('showSnackBar', error.message, 'error');
                })
                .finally(() => {
                    this.loading = false
                })
        },
        closeModal() {
            this.$emit('close-modal')
            this.password = ''
        },
        eventClick() {
            console.log(this.$refs.pass.$refs.input)
            this.$refs.pass.$refs.input.click()
        }
    }
}
</script>

<style lang="scss">
    .action-block {
        form {
            margin-top: 20px;
        }
    }
    .modal__header-title {
        display: flex;
        align-items: flex-end;
        svg {
            margin-right: 10px;
        }
    }
    .modal-footer {
        padding: 30px 0;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        &:after {
            content: "";
            width: 100%;
            height: 4px;
            background-image: -webkit-gradient(linear,right top,left top,from(#ff5404),to(#ffa424));
            background-image: linear-gradient(270deg,#ff5404 0,#ffa424);
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            border-bottom-right-radius: 4px;
            border-bottom-left-radius: 4px;
        }
    }

</style>
