<template>
    <div
        v-show="showModal"
        :class="{'confirm-modal': confirmModal, 'agreement-modal__dialog': agreeModal}"
    >
        <div
            :class="{'confirm-modal__dialog': confirmModal, 'agreement-modal__dialog': agreeModal}"
        >
            <header
                :class="{'confirm-modal__header': confirmModal, 'agreement-modal__header': agreeModal}"
            >
                <slot name="header" />
                <span
                    :class="{'confirm-modal__close': confirmModal, 'agreement-modal__close': agreeModal}"
                    @click="close()"
                >
                    <CloseIcon />
                </span>
            </header>
            <section
                :class="{'confirm-modal__content': confirmModal, 'agreement-modal__content': agreeModal}"
            >
                <slot name="content" />
            </section>
            <footer class="modal-footer">
                <slot name="footer" />
            </footer>
        </div>
    </div>
</template>

<script>
// two types confirm | agree
import CloseIcon from 'mdi-vue/Close.vue';

export default {
    name: 'VModal',
    components: {
        CloseIcon
    },
    props: {
        showModal: Boolean,
        confirmModal: Boolean,
        agreeModal: Boolean
    },
    watch: {
        showModal(newVal) {
            if (newVal === true) {
                this.$emit('openedModal')
            }
        }
    },
    methods: {
        close() {
            this.$emit('close-modal')
        }
    }
}
</script>

